import React from 'react';
export default () => {
  return (
    <div style={{ paddingTop: '50px' }}>
      <h1>
        <span className="text-danger">404</span>
      </h1>
      <h2>Page Not Found</h2>
      <p className="leade">Sorry, that page doesn't exist</p>
    </div>
  );
};



